<template>
  <div class="pay">
    <van-nav-bar fixed title="提交订单" left-text="返回" left-arrow @click-left="back" />
    <div class="pay-box">
      <div class="pay-card adress-box">
        <div class="shipping-address" @click="openAddressBox">
          <span>选择收货地址</span>
          <van-icon size="14" name="arrow" />
        </div>
        <div class="user-name user-info-box">
          <span>用户名：</span>
          <span>{{currentAddress.name}}</span>
        </div>
        <div class="tel-number user-info-box">
          <span>电话号码：</span>
          <span>{{currentAddress.tel}}</span>
        </div>
        <div class="particular-address user-info-box">
            <span>详细地址：</span>
          <span>{{currentAddress.address}}</span>
        </div>
      </div>
      <div class="pay-card order-infor">
        <h3>订单信息</h3>
        <div class="pro-card clearfix" v-for="(item,index) in shopbagDataBySids" :key="index">
          <van-swipe-cell>
            <van-cell :border="false">
              <div class="pro-img fl">
                <img class="auto-img" :src="item.small_img" alt />
              </div>
              <div class="card-info fr">
                <div class="pro-info">
                  <div class="clearfix">
                    <div class="fl pro-name">{{item.name}}</div>
                    <div class="fl pro-rule">{{item.rule}}</div>
                  </div>
                  <div class="pro-enname">{{item.enname}}</div>
                </div>
                <div class="pro-price">
                  <div class="pro-price-text">￥{{item.price}}</div>
                  <div class="pro-price-count">x{{item.count}}</div>
                </div>
              </div>
            </van-cell>
          </van-swipe-cell>
        </div>
      </div>
    </div>
    <van-submit-bar :price="totalPrices" button-color="#0C34BA" @submit="pay" button-text="前往结算" />

    <van-popup v-model="isOpen" closeable round position="bottom" :style="{height:'50%'}">
      <div class="address-title-box">选择收货地址</div>
      <van-address-list
        v-model="chosenAddressId"
        default-tag-text="默认"
        @select="selectAddress"
        @add="newAddress"
        @edit="editAddress"
        :list="list"
      />
    </van-popup>
  </div>
</template>

<script>
import "../assets/css/pay.less";
import {utils} from '../assets/js/utils'
export default {
  data() {
    return {
      isOpen: false,
      chosenAddressId: "",
      totalPrices: 0,
      shopbagDataBySids: [],
      sids: [],
      currentAddress: {},
      list: [],
    };
  },
  created() {
    this.sids = this.$route.query.sids;
    // 获取商品数据
    this.getShopbagDataBySids();
    // 获取地址数据
    this.getShippingAddress();
  },
  methods: {
    // 根据sids获取购物袋数据
    getShopbagDataBySids() {
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
          sids: this.sids,
        },
      })
        .then((result) => {
          this.$toast.clear();
          //  
          if (result.data.code == 50000) {
            this.shopbagDataBySids = result.data.result;
            this.allPrice();
            //  
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },

    // 获取收获地址
    getShippingAddress() {
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      }).then((result) => {
        this.$toast.clear();
        //  
        if (result.data.code == 20000) {
          result.data.result.map((v) => {
            let address = v.province + v.city + v.county + v.addressDetail;
            let addressObj = {
              id: v.aid,
              name: v.name,
              tel: v.tel,
              address,
              isDefault: Boolean(v.isDefault),
            };
            if (Boolean(v.isDefault)) {
              this.chosenAddressId = v.aid;
              this.currentAddress.name = v.name;
              this.currentAddress.tel = v.tel;
              this.currentAddress.address = address;
              this.list.unshift(addressObj);
            } else {
              this.list.push(addressObj);
            }
          });
        }
      });
    },

    // 前往结算
    pay(){
        //判断是否选择地址
        if (this.chosenAddressId == '') {
          this.$toast('请选择收货地址');
          return;
        }

        //获取token字符串
        let tokenString = localStorage.getItem('YXCFTK');
        if (!tokenString) {
          return;
        }

        //参数序列化
        let data = utils.queryString({
          appkey: this.appkey,
          tokenString,
          sids: this.sids,
          phone: this.currentAddress.tel,
          address: this.currentAddress.address,
          receiver: this.currentAddress.name
        })

        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

        this.axios({
          method: 'POST',
          url: '/pay',
          data
        }).then(result => {
          this.$toast.clear();
          //  
          this.$toast(result.data.msg);
          if (result.data.code == 60000) {
            this.$router.push({name: 'Order'});
          }
          
        }).catch(err => {
          this.$toast.clear();
           
        })



    },

    // 返回按钮
    back() {
      this.$router.go(-1);
    },

    // 打开弹出层
    openAddressBox() {
      this.isOpen = true;
    },

    // 选择地址
    selectAddress(address) {
       
      this.currentAddress = address;
      this.isOpen = false;
    },

    // 新建地址
    newAddress() {
      this.$router.push({ name: "NewAddress" });
    },

    // 编辑地址
    editAddress(address) {
      let aid = address.id;
       
      this.$router.push({ name: "NewAddress", query: { aid } });
    },

    // 计算总价
    allPrice() {
      this.totalPrices = 0;
      this.shopbagDataBySids.map((v) => {
        this.totalPrices += v.count * v.price;
      });
      this.totalPrices *= 100;
    },
  },
};
</script>

<style lang="less" scoped>
</style>